 .containerForBars {
    display: inline-block;
    cursor: pointer;
    width: 3%;
    top: 5%;
    right: 7%; 
    position: fixed;
    z-index: 10000;
    
  }
   .bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    background-color: black;
    margin: 6px 0;
    transition: 0.4s;
  }